html {
  height: 100%;
}

body {
  margin: 0 auto;
  font-family: "Inter", sans-serif;
  background-color: #ffffff;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.page__wrapper {
  margin: 0 auto;
  min-width: 320px;
}
@media (min-width: 600px) {
  .page__wrapper {
    width: 600px;
  }
}

.list {
  list-style: none;
  font-size: 13px;
  line-height: 16px;
  padding-left: 30px;
}
.list--links {
  padding-top: 120px;
  margin-bottom: 0;
}
.list--social {
  margin-top: 50px;
  padding-bottom: 40px;
}
@media (min-width: 375px) {
  .list {
    font-size: 15px;
    line-height: 18px;
    padding-left: 46px;
  }
}
@media (min-width: 600px) {
  .list {
    margin-left: 92px;
  }
  .list--links {
    margin-bottom: 0;
  }
  .list--social {
    margin-top: 60px;
    padding-bottom: 50px;
  }
}

.list a {
  text-decoration: none;
  color: #000000;
}
.list a:hover {
  opacity: 0.5;
}

.audio__icon {
  border: none;
  background-color: transparent;
  position: fixed;
  top: 15px;
  right: 16px;
}

.audio__on,
.audio__off {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.picture__first-pic {
  display: none;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (min-width: 600px) {
  .picture__first-pic {
    width: 320px;
    height: 463px;
    margin: 0 auto;
  }
}

.picture__second-pic {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (min-width: 600px) {
  .picture__second-pic {
    width: 500px;
    margin: 0 auto;
    padding: 0;
  }
}

.contact {
  text-decoration: none;
  color: #000000;
  font-size: 13px;
  position: relative;
  bottom: 88px;
  left: 234px;
  padding: 6px 8px;
}
@media (min-width: 375px) {
  .contact {
    font-size: 15px;
    left: 280px;
    bottom: 92px;
  }
}
@media (min-width: 600px) {
  .contact {
    bottom: 102px;
    left: 368px;
  }
}
.contact:hover {
  background-color: rgba(195, 192, 168, 0.948);
}
.contact:active {
  background-color: rgba(169, 166, 144, 0.948);
}